export function getNodeFromSelector(selector) {
  var node = typeof selector === 'string' ? document.querySelector(selector) : selector;
  return node || document.body;
}

export function createContainerNode(parent) {
  var tag = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : 'div';

  var div = document.createElement(tag);
  return parent.appendChild(div);
}

export function removeNodeFromDOMTree(node) {
  var parentNode = node.parentNode;

  if (parentNode) {
    parentNode.removeChild(node);
  }
}

export function isDescendant(parent, child) {
  var node = child.parentNode;

  while (node !== null) {
    if (node === parent) return true;
    node = node.parentNode;
  }

  return false;
}