import createPlacement from './create';

/**
 *           |--------------------|
 * ----------|                    |
 * | popover |   anchor           |
 * |---------|                    |
 *           |--------------------|
 */
function locate(anchorBoundingBox, containerBoundingBox, contentDimension, options) {
  var x = anchorBoundingBox.left - contentDimension.width - options.cushion;
  var middle = (anchorBoundingBox.top + anchorBoundingBox.bottom) / 2;
  var y = middle - contentDimension.height / 2;

  return {
    getCSSStyle: function getCSSStyle() {
      return {
        position: 'absolute',
        left: Math.round(x) + 'px',
        top: Math.round(y) + 'px'
      };
    },


    name: 'position-left-center'
  };
}

var LeftCenter = createPlacement(locate);

export default LeftCenter;