import createPlacement from './create';

/**
 * 不可见定位
 */
function locate() {
  var x = -100000;
  var y = -100000;

  return {
    getCSSStyle: function getCSSStyle() {
      return {
        position: 'fixed',
        left: x + 'px',
        top: y + 'px',
        zIndex: -10,
        opacity: 0
      };
    },


    name: 'position-invisible'
  };
}

var Invisible = createPlacement(locate);

export default Invisible;