import createPlacement from './create';

/**
 *        ---------
 *        |popover|
 * ----------------
 * |   anchor     |
 * |              |
 * ----------------
 */
function locate(anchorBoundingBox, containerBoundingBox, contentDimension, options) {
  var right = anchorBoundingBox.right,
      top = anchorBoundingBox.top;

  var x = right - contentDimension.width;
  var y = top - contentDimension.height - options.cushion;

  return {
    getCSSStyle: function getCSSStyle() {
      return {
        position: 'absolute',
        left: Math.round(x) + 'px',
        top: Math.round(y) + 'px'
      };
    },


    name: 'position-top-right'
  };
}

var TopRight = createPlacement(locate);

export default TopRight;