import createPlacement from './create';

/**
 * ---------------
 * |   anchor     |
 * |              |
 * ---------------
 *    |popover|
 *    ---------
 */
function locate(anchorBoundingBox, containerBoundingBox, contentDimension, options) {
  var left = anchorBoundingBox.left,
      right = anchorBoundingBox.right,
      bottom = anchorBoundingBox.bottom;

  var middle = (left + right) / 2;
  var x = middle - contentDimension.width / 2;
  var y = bottom + options.cushion;

  return {
    getCSSStyle: function getCSSStyle() {
      return {
        position: 'absolute',
        left: Math.round(x) + 'px',
        top: Math.round(y) + 'px'
      };
    },


    name: 'position-bottom-center'
  };
}

var BottomCenter = createPlacement(locate);

export default BottomCenter;