import createPlacement from './create';

/**
 * ----------------------
 * |                    |----------
 * |   anchor           | popover |
 * |--------------------|---------
 *
 */
function locate(anchorBoundingBox, containerBoundingBox, contentDimension, options) {
  var right = anchorBoundingBox.right,
      bottom = anchorBoundingBox.bottom;

  var x = right + options.cushion;
  var y = bottom - contentDimension.height;

  return {
    getCSSStyle: function getCSSStyle() {
      return {
        position: 'absolute',
        left: Math.round(x) + 'px',
        top: Math.round(y) + 'px'
      };
    },


    name: 'position-right-bottom'
  };
}

var RightBottom = createPlacement(locate);

export default RightBottom;